'use client'

import { Suspense } from 'react'
import Link from 'next/link'
import { Navbar } from '@/components/navbar'
import { AnimeCard } from '@/components/anime-card'
import { getTopAnime, getPopularAnime, Anime } from '@/lib/api'
import { HeroSkeleton, StaticSkeletonGrid, FlexSkeletonGrid } from '@/components/skeleton-loader'

// ========================
// Hero Section
// ========================
async function HeroSection() {
  const animes: Anime[] = (await getTopAnime(1))?.slice(0, 5) || []
  const { HeroCarousel } = await import('@/components/hero-carousel') // dynamic import client component

  return (
    <div className="mt-8 mb-12 max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
      <HeroCarousel animes={animes} />
    </div>
  )
}

// ========================
// Top Anime Section
// ========================
async function TopSection() {
  const animes: Anime[] = (await getTopAnime(1))?.slice(0, 5) || []

  return (
    <div className="mb-12 max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
      <h2 className="text-xl sm:text-2xl font-bold text-foreground mb-3 sm:mb-4">
        Top Recent Anime
      </h2>

      <div className="grid grid-cols-2 sm:grid-cols-3 md:grid-cols-3 lg:grid-cols-5 gap-3 sm:gap-4 md:gap-6">
        {animes.map((anime: Anime, index: number) => (
          <Link key={anime.animeId || anime.mal_id} href={`/anime/${anime.animeId || anime.mal_id}`}>
            <AnimeCard anime={anime} />
          </Link>
        ))}
      </div>
    </div>
  )
}

// ========================
// Popular Section
// ========================
async function PopularSection() {
  const animes: Anime[] = (await getPopularAnime(1)) || []

  return (
    <div className="mb-12 max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
      <h2 className="text-2xl font-bold text-foreground mb-4">Popular Anime</h2>
      <div className="grid grid-cols-2 md:grid-cols-3 lg:grid-cols-4 xl:grid-cols-5 gap-4 md:gap-6">
        {animes.map((anime: Anime, index: number) => (
          <Link key={anime.animeId || anime.mal_id} href={`/anime/${anime.animeId || anime.mal_id}`}>
            <AnimeCard anime={anime} />
          </Link>
        ))}
      </div>
    </div>
  )
}

// ========================
// Trending Section (More Recent)
// ========================
async function TrendingSection() {
  const animes: Anime[] = (await getTopAnime(1))?.slice(5, 25) || []

  return (
    <div className="mb-12 max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
      <h2 className="text-2xl font-bold text-foreground mb-4">More Recent Releases</h2>
      <div className="grid grid-cols-2 md:grid-cols-3 lg:grid-cols-4 xl:grid-cols-5 gap-4 md:gap-6">
        {animes.map((anime: Anime, index: number) => (
          <Link key={anime.animeId || anime.mal_id} href={`/anime/${anime.animeId || anime.mal_id}`}>
            <AnimeCard anime={anime} />
          </Link>
        ))}
      </div>
    </div>
  )
}

// ========================
// Home Page
// ========================
export default function Home() {
  return (
    <main className="min-h-screen bg-background">
      <Navbar />

      {/* Hero Carousel */}
      <Suspense fallback={<HeroSkeleton />}>
        <HeroSection />
      </Suspense>

      {/* Top Recent */}
      <Suspense fallback={<StaticSkeletonGrid count={5} />}>
        <TopSection />
      </Suspense>

      {/* Popular */}
      <Suspense fallback={<FlexSkeletonGrid count={25} />}>
        <PopularSection />
      </Suspense>

      {/* More Recent Releases */}
      <Suspense fallback={<FlexSkeletonGrid count={20} />}>
        <TrendingSection />
      </Suspense>

      {/* Footer */}
      <footer className="border-t border-border bg-card mt-16">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">

          {/* Footer Links */}
          <div className="flex flex-wrap justify-center items-center gap-8 text-muted-foreground text-sm mb-6">
            <Link href="/" className="flex items-center gap-2 hover:text-primary transition-colors">Home</Link>
            <Link href="/popular" className="flex items-center gap-2 hover:text-primary transition-colors">Popular</Link>
            <Link href="/trending" className="flex items-center gap-2 hover:text-primary transition-colors">Trending</Link>
            <a href="#" className="flex items-center gap-2 hover:text-primary transition-colors">About</a>
            <a href="#" className="flex items-center gap-2 hover:text-primary transition-colors">Contact</a>
            <a href="#" className="flex items-center gap-2 hover:text-primary transition-colors">FAQ</a>
          </div>

          {/* Copyright */}
          <div className="border-t border-border pt-6 text-center text-muted-foreground text-sm">
            <p>Stream4nzu Â© 2025. All rights reserved.</p>
          </div>
        </div>
      </footer>
    </main>
  )
}
