// lib/api.ts
import axios from 'axios';

export const API_BASE = 'http://localhost:4000/anime/zoro';

export interface Anime {
  animeId?: string;
  mal_id?: string;
  title: string;
  image: string;
  url: string;
  type: string;
  episodes?: number;
  duration?: string;
  sub?: number;
  dub?: number;
  japaneseTitle?: string;
  nsfw?: boolean;
}

// ============================
// Helper function untuk fetch
// ============================
async function fetchAnime(endpoint: string, page: number = 1): Promise<Anime[]> {
  try {
    const res = await axios.get(`${API_BASE}/${endpoint}?page=${page}`);
    const data = res.data?.results || [];
    return data.map((item: any) => ({
      animeId: item.id,
      title: item.title,
      image: item.image,
      url: item.url,
      type: item.type,
      episodes: item.episodes,
      duration: item.duration,
      sub: item.sub,
      dub: item.dub,
      japaneseTitle: item.japaneseTitle,
      nsfw: item.nsfw
    }));
  } catch (err) {
    console.error(`Error in fetchAnime(${endpoint}):`, err);
    return [];
  }
}

// ============================
// API functions
// ============================

// Recent episodes (default page=1)
export async function getRecentEpisodes(page: number = 1): Promise<Anime[]> {
  return fetchAnime('recent-episodes', page);
}

// Popular anime (default page=1)
export async function getPopularAnime(page: number = 1): Promise<Anime[]> {
  return fetchAnime('popular', page);
}

// Top anime (default page=1)
export async function getTopAnime(page: number = 1): Promise<Anime[]> {
  return fetchAnime('top', page);
}

// Search anime by query
export async function searchAnime(query: string, page: number = 1): Promise<Anime[]> {
  try {
    const res = await axios.get(`${API_BASE}/search?query=${encodeURIComponent(query)}&page=${page}`);
    const data = res.data?.results || [];
    return data.map((item: any) => ({
      animeId: item.id,
      title: item.title,
      image: item.image,
      url: item.url,
      type: item.type,
      episodes: item.episodes,
      duration: item.duration,
      sub: item.sub,
      dub: item.dub,
      japaneseTitle: item.japaneseTitle,
      nsfw: item.nsfw
    }));
  } catch (err) {
    console.error('Error in searchAnime:', err);
    return [];
  }
}

// Get anime detail by ID
export async function getAnimeDetail(animeId: string): Promise<Anime | null> {
  try {
    const res = await axios.get(`${API_BASE}/info/${animeId}`);
    const item = res.data;
    if (!item) return null;

    return {
      animeId: item.id,
      title: item.title,
      image: item.image,
      url: item.url,
      type: item.type,
      episodes: item.episodes,
      duration: item.duration,
      sub: item.sub,
      dub: item.dub,
      japaneseTitle: item.japaneseTitle,
      nsfw: item.nsfw
    };
  } catch (err) {
    console.error('Error in getAnimeDetail:', err);
    return null;
  }
}
